import uniFab from '../../../components/uni-fab/uni-fab.vue'
// 请求封装
import reqeust from '../../../util/request.js'
export default {
	data() {
		return {
			isShare : false,
			shareModal: false,
			id: null,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			TabCur: 0,
			scrollLeft: 0,
			movie: {},
			pattern: {
				color: '#7A7E83',
				backgroundColor: '#fff',
				selectedColor: '#d81e06',
				buttonColor: '#d81e06'
			},
			contents: [{
					iconPath: "../../static/fab/home.svg",
					selectedIconPath: "../../static/fab/home-select.svg",
					text: "首页",
					active: true
				},
				{
					iconPath: "../../static/fab/zan.svg",
					selectedIconPath: "../../static/fab/zan-select.svg",
					text: "红包",
					active: true
				},
				/* {
					iconPath: "../../static/fab/quan.svg",
					selectedIconPath: "../../static/fab/quan-select.svg",
					text: "会员券",
					active: true
				} */
			],
			horizontal: 'right',
			vertical: 'top',
			direction: 'horizontal',
			zanModal: false,
			playModal: false,
			url: "",
			remark: "",
			showTip: true
		}
	},
	onLoad: function(option) {
		this.id = option.id
		this.getDetail()
	},
	created: function() {},
	methods: {
		tabSelect: function(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		trigger(e) {
			console.log(e);
			/* 首页 */
			if (e.index == 0) {
				uni.redirectTo({
					url: '/pages/index/index'
				});
				return;
			} else if (e.index == 1) {
				this.zanModal = true
				return;
			}
		},
		hideModal(e) {
			this.zanModal = false
			this.playModal = false
			this.url = ""
		},
		playMovie: function(e) {
			//检验是否已经登陆
			let userInfo = uni.getStorageSync('loginInfo')
			if (userInfo ==null || userInfo == '') {
				uni.navigateTo({
				    url: '/pages/component/login/index'
				});
			}
			
			let playUrl = e.currentTarget.dataset.url
			this.playModal = true
			this.url = playUrl
			this.remark = e.currentTarget.dataset.order
			if (!this.isShare) {
				this.shareModal = true
			}
			
		},
		getDetail: function() {
			let _that = this;
			reqeust.get(reqeust.url.DETAIL + _that.id, null).then(res => {
				_that.movie = res
			})
		},
		copyUrl: function(e) {
			let _that = this
			uni.setClipboardData({
				data: e.currentTarget.dataset.url,
				success: function(res) {
					uni.showToast({
						icon: 'none',
						title: '复制成功请到浏览器打开'
					});
				}
			});
		},
		onShareAppMessage:function() {
			let _that = this;
			this.shareModal = false
			this.isShare = true
			return {
				title: "分享一部好看的电影给你",
				path: '/pages/component/movie-tpl/movie-detail?id=' + this.id,
			}
		}
	},
	components: {
		uniFab
	}
}
